:root {
	--clock-fill-color: #000;
	--clock-hand-color: #fff;
	--clock-text-color: #fff;
	--clock-text-size: 16px;
	--clock-size: 160px;
	--hour-hand-stroke-width: 6px;
	--minute-hand-stroke-width: 3px;
	--second-hand-stroke-width: 2px;
}

.label {
	text-align: center;
}

.clock {
	fill: #000;
}

.hourText {
	fill: #fff;
	fill: var(--clock-text-color);
	font-size: 16px;
	font-size: var(--clock-text-size);
}

.hand {
	stroke: #fff;
}

.hourHand {
	stroke-width: 6px;
	stroke-width: var(--hour-hand-stroke-width);
}

.minuteHand {
	stroke-width: 3px;
	stroke-width: var(--minute-hand-stroke-width);
}

.secondHand {
	stroke-width: 2px;
	stroke-width: var(--second-hand-stroke-width);
}

.joint {
	stroke: #fff;
}



